import { cilPencil, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import { CButton, CCol, CFormInput, CFormSelect, CRow, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

const DataTable = ({ data, headers, onDelete }) => {
  const [sortedData, setSortedData] = useState(data);
  const [filterData, setFilterData] = useState(sortedData);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortField, setSortField] = useState('');
  const [sortOrder, setSortOrder] = useState('ascending');
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(15);

  const sortData = (key) => {
    let newSortOrder = 'ascending';
    if (sortField === key && sortOrder === 'ascending') {
      newSortOrder = 'descending';
    }
    setSortOrder(newSortOrder);

    const sorted = [...sortedData].sort((a, b) =>
      newSortOrder === 'ascending'
        ? a[key].localeCompare(b[key])
        : b[key].localeCompare(a[key])
    );
    setSortedData(sorted);
    setSortField(key);
  };

  const searchFilter = (e) => {
    setSearchTerm(e.target.value);
    setCurrentPage(1);
  };

  const handlePerPageChange = (e) => {
    setItemsPerPage(parseInt(e.target.value));
    setCurrentPage(1);
  };

  useEffect(() => {
    setSortedData(data);
    setFilterData(data);
    setCurrentPage(1);
  }, [data]);

  useEffect(() => {
    if (searchTerm) {
      const re = new RegExp('^'+searchTerm, "i"); 
      const filteredData = sortedData.filter((item) =>        
        item.name.match(re)
      );
      setFilterData(filteredData);
    } else {
      setFilterData(sortedData);
    }
    setCurrentPage(1);
  }, [searchTerm, sortedData]);

  // Pagination Logic
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filterData.slice(indexOfFirstItem, indexOfLastItem);

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  return (
    <div>
      <CRow className='align-items-end mb-3 arSearchAndShowPerpg '>
      <CCol xl={4}>
        <label>Show per page:</label>
        <CFormSelect value={itemsPerPage} onChange={handlePerPageChange}>
          <option value={15}>15</option>
          <option value={30}>30</option>
          <option value={50}>50</option>
          <option value={100}>100</option>
        </CFormSelect>
      </CCol>
      <CCol xl={8}>
        <CFormInput
          type="text"
          placeholder="Search by name"
          onChange={searchFilter}
        />
      </CCol>
      
      </CRow>

      <CTable border={1} striped>
        <CTableHead>
          <CTableRow>
            {headers.map((header, key) => (
              <CTableHeaderCell
                width={header.width}
                key={key}
                onClick={header.short ? () => sortData(header.field) : null}
                className={`${header.shortby ? header.shortby : ''} ${
                  header.short ? 'shortable' : ''
                }`}
              >
                {header.name}
                {header.short && sortField === header.field && (
                  <span className="sort-indicator">
                    {sortOrder === 'ascending' ? '↑' : '↓'}
                  </span>
                )}
              </CTableHeaderCell>
            ))}
          </CTableRow>
        </CTableHead>
        <CTableBody>
          {currentItems.map((item, ikey) => (
            <CTableRow key={ikey}>
              {headers.map((header, j) => (
                <CTableDataCell key={j}>
                  {header.type === 'image' && item[header.field] ? (
                    <Link to={"./"+item._id}>
                      <img src={process.env.REACT_APP_BASE_URL + item[header.field].image} width={100} />
                    </Link>
                  ) : header.type === 'status'? (<CButton color={item[header.field]?"sucess":"warning"} >{item[header.field]?"Active":"inactive"}</CButton>): header.type === 'action' ? (
                    // JSX code for the 'action' type
                    <ul className='actionBtnS'>
                      <li><Link to={'./'+item["_id"]+'/edit'}><CIcon icon={cilPencil} customClassName="nav-icon" /></Link></li>
                      <li><div onClick={()=>{onDelete(item["_id"])}}><CIcon icon={cilTrash} customClassName="nav-icon" /></div></li>
                    </ul>
                  ) : (<>{item[header.field]}</>)}


                </CTableDataCell>
              ))}
            </CTableRow>
          ))}
        </CTableBody>
      </CTable>
      
      <div>
        {filterData.length > itemsPerPage && (
          <ul className="pagination">
            {Array(Math.ceil(filterData.length / itemsPerPage))
              .fill()
              .map((_, i) => (
                <li
                  key={i}
                  className={`page-item ${currentPage === i + 1 ? 'active' : ''}`}
                  onClick={() => paginate(i + 1)}
                >
                  <span className="page-link">{i + 1}</span>
                </li>
              ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default DataTable;
